:root {
    --gap-small: 6px;
    --gap-medium: 12px;
    --mt-medium: 12px;

    --color-sub: #5e5e5e;
}

/*
-BODY CONTAINER
*/
.body-container {
  padding-top: 16px;
  padding-bottom: 16px;
  max-width: 1200px;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: var(--gap-medium);
}

.body-container.side-padding {
  padding-left: 16px;
  padding-right: 16px;
}

/*
-FLEX BOX
*/
.flex {
    display: flex;
    flex-direction: row;
}

/*
-BUTTON
*/
Button, .button {
  display: flex;
  align-items: center;
  gap: var(--gap-small);
}

.icon-button {
    min-width: 16px;
    gap: var(--gap-small);
}

.menu-toolbar {
    Button {
        width: min-content;
        height: min-content;
        align-self: center;
    }
}

/*
-TEXT
*/
.subtitle {
    color: var(--color-sub);
    margin: 0;
}

p, b {
    align-items: center;
    display: flex;
    gap: var(--gap-small);
}

/*
-CARD
*/
.card {
    display: flex !important;
    flex-direction: row !important;
    justify-content: space-between !important;
    align-content: center !important;
    gap: var(--gap-medium) !important;
}

.card-content{
    display: flex;
    flex-direction: column;
    gap: 0;
}

/*
-MENU TOOLBAR
*/
.menu-toolbar {
    display: flex;
    width: 100%;
    gap: var(--gap-small);

    overflow-x: auto;

    p, b {
        margin-right: var(--gap-medium);
    }
}

/*
-POPOVER
*/
.popover-dialog-content {
    display: flex;
    flex-direction: column;
    gap: var(--gap-medium);
}

/*
-DIALOG
*/

.dialog-content-container {
    display: flex;
    flex-direction: column;
    gap: var(--gap-medium);
    padding-top: var(--gap-medium);
    padding-bottom: var(--gap-medium);

    .dialog-content-item {
        display: flex;
        flex-direction: column;
        gap: var(--gap-small);

        Label {
            max-width: 150px;
        }

        Input {
            flex: 1;
        }
    }
}

/*
-NAV DRAWER
*/

.nav-drawer {
    padding: var(--gap-small);
}

.nav-drawer-header {
    display: flex;
    align-items: end;
    margin-top: var(--mt-medium);
}

.nav-drawer-body {
    padding-top: var(--mt-medium);
    display: flex;
    flex-direction: column;
    gap: var(--gap-medium);
}